@use '../../src/sass/variables' as variables;

.post-blocks {
    &__heading{
        color: variables.$navyBlueDark;
        font-family: 'Roboto Flex', sans-serif;
        font-size: 36px;
        font-style: normal;
        font-weight: 800;
        line-height: 130%;
        margin-bottom: 65px;
        margin-top: 140px;
    }

    .post-box{
        display: grid;
        grid-template-columns: 70% 30%;
        grid-template-rows: auto auto;
        padding-bottom: 40px;
        margin-bottom: 40px;
        border-bottom: 1px solid variables.$navyBlueLight2;

        &__meta{
            display: flex;
            flex-direction: row;
            margin-bottom: 25px;

            .post-box__categories{
                .post-box__category{
                    .post__link{
                        background-color: variables.$navyBlueLight2;
                        border-radius: 6px 0px 0px 6px;
                        padding: 8px 12px 10px 12px;
                        display: inline-block;
                        color: variables.$navyBlue;
                        font-family: 'Roboto Flex';
                        font-size: 13px;
                        font-style: normal;
                        font-weight: 600;
                        line-height: 120%;
                        text-transform: uppercase;
                        transition: all 0.2s ease;
                        
                        &:hover{
                            background-color: variables.$navyBlue;
                            color: variables.$almostWhite;
                            transition: all 0.2s ease;
                        }
                    }
                }
            }

            .post-box__data{
                font-family: 'Roboto Flex';
                font-size: 13px;
                font-style: normal;
                font-weight: 600;
                line-height: 120%;
                text-transform: uppercase;
                border-radius: 0px 6px 6px 0px;
                padding: 8px 12px 10px 12px;
            }
        }

        &__title{
            color: variables.$navyBlue;
            font-family: 'Roboto Flex';
            font-size: 29px;
            font-style: normal;
            font-weight: 800;
            line-height: 130%;
            margin-bottom: 13px;
        }

        &__excerpt{
            color: variables.$navyBlue;
            font-family: 'Roboto Flex';
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: 150%;
        }
    }
}